"use client"

import * as React from "react"
import Link from "next/link"
import { ArrowLeft } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"

export interface PageHeaderProps extends React.HTMLAttributes<HTMLDivElement> {
  /**
   * Если true - sticky позиционирование под Header
   * @default true
   */
  sticky?: boolean
  
  /**
   * Максимальная ширина контента
   */
  maxWidth?: "5xl" | "6xl" | "7xl" | "full"
  
  /**
   * Контент - любые элементы
   */
  children: React.ReactNode
}

/**
 * Компактный универсальный PageHeader - ОДНА строка, 56px высоты
 * Размещает любой контент горизонтально
 */
export function PageHeader({
  sticky = true,
  maxWidth = "full",
  className,
  children,
  ...props
}: PageHeaderProps) {
  const maxWidthClasses = {
    "5xl": "max-w-5xl",
    "6xl": "max-w-6xl", 
    "7xl": "max-w-7xl",
    "full": "w-full",
  }

  return (
    <div
      className={cn(
        "bg-gray-50 border-b border-border",
        sticky && "sticky top-[56px] z-40",
        className
      )}
      {...props}
    >
      <div className={cn("mx-auto px-6 h-14 flex items-center justify-between gap-4", maxWidthClasses[maxWidth])}>
        {children}
      </div>
    </div>
  )
}

// ============= Вспомогательные компоненты =============

/**
 * Левая секция - навигация и заголовок
 */
export function PageHeaderLeft({ children, className }: { children: React.ReactNode; className?: string }) {
  return <div className={cn("flex items-center gap-3 min-w-0 flex-1", className)}>{children}</div>
}

/**
 * Правая секция - действия
 */
export function PageHeaderRight({ children, className }: { children: React.ReactNode; className?: string }) {
  return <div className={cn("flex items-center gap-2 flex-shrink-0", className)}>{children}</div>
}

/**
 * Кнопка "Назад"
 */
export function PageHeaderBack({ 
  href, 
  label = "Назад",
  onClick,
  className 
}: { 
  href: string
  label?: string
  onClick?: (e: React.MouseEvent) => void
  className?: string 
}) {
  return (
    <Link
      href={href}
      onClick={onClick}
      className={cn(
        "inline-flex items-center gap-1.5 text-sm text-muted-foreground hover:text-foreground transition-colors flex-shrink-0",
        className
      )}
    >
      <ArrowLeft className="h-4 w-4" />
      <span className="hidden sm:inline">{label}</span>
    </Link>
  )
}

/**
 * Заголовок
 */
export function PageHeaderTitle({ children, className }: { children: React.ReactNode; className?: string }) {
  return (
    <h1 className={cn("text-lg font-semibold truncate", className)}>
      {children}
    </h1>
  )
}

/**
 * Бейдж/счётчик
 */
export function PageHeaderBadge({ children, className }: { children: React.ReactNode; className?: string }) {
  return (
    <span className={cn("px-2 py-0.5 text-xs font-medium bg-gray-100 text-muted-foreground rounded-md flex-shrink-0", className)}>
      {children}
    </span>
  )
}

/**
 * Разделитель
 */
export function PageHeaderSeparator({ className }: { className?: string }) {
  return <div className={cn("h-4 w-px bg-border flex-shrink-0", className)} />
}
