"use client"

import { useState } from "react"
import Link from "next/link"
import { PageHeader } from "@/components/layout/page-header"
import {
  Trash2,
  Pencil,
  Calendar,
  Clock,
  Mail,
  Phone,
  Landmark,
  CreditCard,
  FileText,
  Download,
  ChevronDown,
} from "lucide-react"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Button } from "@/components/ui/button"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import type { User, UserDocument } from "@/types/user"
import { InfoCard } from "./info-card"
import { InfoRow } from "./info-row"
import { DocumentItem } from "./document-item"
import { ScanPreviewModal } from "./scan-preview-modal"

interface UserProfileProps {
  user: User
}

const statusLabels = {
  active: "Активный",
  inactive: "Неактивный",
  blocked: "Заблокирован",
}

const statusColors = {
  active: "bg-green-500",
  inactive: "bg-gray-400",
  blocked: "bg-red-500",
}

export function UserProfile({ user }: UserProfileProps) {
  const [status, setStatus] = useState(user.status)
  const [previewDocument, setPreviewDocument] = useState<UserDocument | null>(null)
  const [isPreviewOpen, setIsPreviewOpen] = useState(false)

  const fullName = `${user.lastName} ${user.firstName} ${user.middleName}`
  const initials = `${user.lastName[0]}${user.firstName[0]}`

  const passportDocuments = user.documents.filter((d) => d.category === "passport")
  const otherDocuments = user.documents.filter((d) => d.category === "other")

  const handlePreview = (doc: UserDocument) => {
    setPreviewDocument(doc)
    setIsPreviewOpen(true)
  }

  const handleClosePreview = () => {
    setIsPreviewOpen(false)
    setPreviewDocument(null)
  }

  const handleDownload = (doc: UserDocument) => {
    console.log("Downloading:", doc.name)
  }

  return (
    <div className="min-h-screen bg-gray-50/50">
      <ScanPreviewModal document={previewDocument} isOpen={isPreviewOpen} onClose={handleClosePreview} />

      <PageHeader
        backButton={{
          href: "/users",
          label: "Вернуться к пользователям",
        }}
        title={fullName}
        actions={
          <>
            <Button variant="outline" size="icon" className="h-9 w-9 bg-transparent">
              <Trash2 className="h-4 w-4" />
            </Button>
            <Link href={`/users/${user.id}/edit`}>
              <Button variant="outline" size="icon" className="h-9 w-9 bg-transparent">
                <Pencil className="h-4 w-4" />
              </Button>
            </Link>
          </>
        }
        maxWidth="5xl"
      />

      <div className="max-w-5xl mx-auto px-6 py-6">

        <div className="bg-white border border-gray-200 rounded-xl p-6 mb-6">
          <div className="flex items-start justify-between mb-6">
            <div className="flex items-center gap-4">
              <Avatar className="h-14 w-14">
                <AvatarImage src={user.avatar || "/placeholder.svg"} />
                <AvatarFallback className="text-lg">{initials}</AvatarFallback>
              </Avatar>
              <div>
                <div className="flex items-center gap-3 mb-1">
                  <h1 className="text-lg font-semibold text-gray-900">{fullName}</h1>
                  <span className={`px-2.5 py-0.5 text-xs font-medium bg-gray-100 text-gray-700 rounded-full`}>
                    {user.role}
                  </span>
                </div>
                <p className="text-sm text-gray-500">
                  {user.age} года • {user.birthDate}
                </p>
              </div>
            </div>
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="outline" className="gap-2 bg-transparent">
                  <span className={`h-2 w-2 rounded-full ${statusColors[status]}`} />
                  {statusLabels[status]}
                  <ChevronDown className="h-4 w-4 text-gray-400" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end">
                <DropdownMenuItem onClick={() => setStatus("active")}>
                  <span className="h-2 w-2 rounded-full bg-green-500 mr-2" />
                  Активный
                </DropdownMenuItem>
                <DropdownMenuItem onClick={() => setStatus("inactive")}>
                  <span className="h-2 w-2 rounded-full bg-gray-400 mr-2" />
                  Неактивный
                </DropdownMenuItem>
                <DropdownMenuItem onClick={() => setStatus("blocked")}>
                  <span className="h-2 w-2 rounded-full bg-red-500 mr-2" />
                  Заблокирован
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>

          <div className="grid grid-cols-2 gap-8">
            <div>
              <h4 className="text-sm font-medium text-gray-900 mb-3">Системная информация</h4>
              <div className="flex gap-8">
                <div className="flex items-center gap-2">
                  <Calendar className="h-4 w-4 text-gray-400" />
                  <div>
                    <p className="text-xs text-gray-500">Дата регистрации</p>
                    <p className="text-sm font-medium text-gray-900">{user.registrationDate}</p>
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  <Clock className="h-4 w-4 text-gray-400" />
                  <div>
                    <p className="text-xs text-gray-500">Последний вход</p>
                    <p className="text-sm font-medium text-gray-900">{user.lastLogin}</p>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <h4 className="text-sm font-medium text-gray-900 mb-3">Контактная информация</h4>
              <div className="flex gap-8">
                <div className="flex items-center gap-2">
                  <Mail className="h-4 w-4 text-gray-400" />
                  <div>
                    <p className="text-xs text-gray-500">Email</p>
                    <p className="text-sm font-medium text-gray-900">{user.email}</p>
                  </div>
                </div>
                <div className="flex items-center gap-2">
                  <Phone className="h-4 w-4 text-gray-400" />
                  <div>
                    <p className="text-xs text-gray-500">Телефон</p>
                    <p className="text-sm font-medium text-gray-900">{user.phone}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-2 gap-6">
          <InfoCard
            icon={Landmark}
            title="Банковские реквизиты"
            footer={
              <Button variant="outline" className="w-full gap-2 bg-transparent">
                <Download className="h-4 w-4" />
                Скачать реквизиты в PDF
              </Button>
            }
          >
            <InfoRow label="Номер карты" value={user.bankDetails.cardNumber} copyable />
            <InfoRow label="БИК банка" value={user.bankDetails.bik} copyable />
            <InfoRow label="Расчетный счет" value={user.bankDetails.accountNumber} copyable />
            <InfoRow label="Получатель" value={user.bankDetails.recipient} copyable />
            <InfoRow label="Корр. счет" value={user.bankDetails.corrAccount} copyable />
          </InfoCard>

          <InfoCard icon={CreditCard} title="Паспортные данные">
            <div className="py-2.5">
              <p className="text-sm text-gray-500 mb-1">Серия и номер</p>
              <div className="flex items-center justify-between">
                <p className="text-sm font-medium text-gray-900">
                  {user.passport.series} {user.passport.number}
                </p>
              </div>
            </div>
            <div className="py-2.5">
              <p className="text-sm text-gray-500 mb-1">Кем выдан</p>
              <p className="text-sm font-medium text-gray-900">{user.passport.issuedBy}</p>
            </div>
            <div className="py-2.5">
              <p className="text-sm text-gray-500 mb-1">Дата выдачи</p>
              <p className="text-sm font-medium text-gray-900">{user.passport.issueDate}</p>
            </div>
            <div className="py-2.5">
              <p className="text-sm text-gray-500 mb-1">Место регистрации</p>
              <p className="text-sm font-medium text-gray-900">{user.passport.registrationAddress}</p>
            </div>
            <div className="pt-4">
              <p className="text-sm font-medium text-gray-900 mb-3">Документы ({passportDocuments.length})</p>
              <div className="space-y-2">
                {passportDocuments.map((doc) => (
                  <DocumentItem
                    key={doc.id}
                    name={doc.name}
                    size={doc.size}
                    type={doc.type}
                    onPreview={() => handlePreview(doc)}
                    onDownload={() => handleDownload(doc)}
                  />
                ))}
              </div>
            </div>
          </InfoCard>

          <InfoCard icon={FileText} title="Прочие данные">
            <InfoRow label="ИНН" value={user.otherData.inn} copyable />
            <InfoRow label="БИК банка" value={user.otherData.snils} copyable />
            <div className="pt-4">
              <p className="text-sm font-medium text-gray-900 mb-3">Документы ({otherDocuments.length})</p>
              <div className="space-y-2">
                {otherDocuments.map((doc) => (
                  <DocumentItem
                    key={doc.id}
                    name={doc.name}
                    size={doc.size}
                    type={doc.type}
                    onPreview={() => handlePreview(doc)}
                    onDownload={() => handleDownload(doc)}
                  />
                ))}
              </div>
            </div>
          </InfoCard>
        </div>
      </div>
    </div>
  )
}
